import { Breadcrumbs, Button, List, ListItem, ListItemText, Typography } from "@material-ui/core";
import * as React from "react";
import { useUserInfo } from "shared/components/save";
import Defer from "shared/components/ui/Defer";
import Navbar from "shared/components/ui/Navbar";

import { useFetcher, useFetcherPaginated } from "shared/components/ajax";
import { humanizeTime, rupiah } from "shared/components/util";
import { navigate } from "gatsby";


const Page = () => {
    const user = useUserInfo();
    const { list, more, canMore, fetching } = useFetcherPaginated((s) => '/debit/history/?start_from=' + (s || ''));
    return (
        <>
            <Navbar />
            <List>
                <ListItem>
                    <Breadcrumbs>
                        <Typography color="textPrimary">Arsip Pembelian</Typography>
                    </Breadcrumbs>
                </ListItem>
                <Defer data={user && list}>
                    {() => <div>
                        {list.map(x => (
                            <ListItem button key={x.id} onClick={() => navigate('/digital/detail?id=' + x.id)}>
                                <ListItemText primary={x.title} secondary={humanizeTime(Date.parse(x.timestamp))} />
                                <ListItemText primary={rupiah(x.diff)} style={{ textAlign: 'right' }} />
                            </ListItem>
                        ))}
                        {canMore && !fetching && list.length % 25 === 0 && <ListItem>
                            <Button onClick={() => more()} variant="contained">Muat lebih banyak</Button>
                        </ListItem>}
                    </div>
                    }
                </Defer>
            </List>
        </>
    );
}

export default Page
